<template>
  <div class="home">
    <v-leftcard v-if="isPortrait == false && isPhone === false"></v-leftcard>
    <div style="width: 98%">
      <el-row align="middle" style="width: 100%">
        <el-button @click="toGo('biography')"><a>Homepage</a></el-button>
        <el-button @click="toGo('research')"><a>Research</a></el-button>
        <el-button @click="toGo('publications')"><a>Publications</a></el-button>
        <el-button @click="toGo('services')"><a>Services</a></el-button>
        <el-button @click="toGo('people')"><a>People</a></el-button>
      </el-row>
      <el-divider></el-divider>
      <div
        :style="
          'height:' +
            windowHeight +
            'px;' +
            'overflow: auto; text-align: left; font-size: 18px; line-height: 1.75; padding: 0% 2% 0% 3%; overflow-y: scroll;'
        "
        id="#top"
      >
        <v-centercard
          v-if="isPortrait === true || isPhone === true"
          style=""
        ></v-centercard>
        <v-biography id="biography"></v-biography>
        <v-research id="research"></v-research>
        <v-publications id="publications"></v-publications>
        <v-services id="services"></v-services>
        <v-people id="people"></v-people>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  methods: {
    toGo(val) {
      console.log(val);
      document.querySelector("#" + val).scrollIntoView(true);
    }
  },
  mounted() {
    let that = this;
    if (
      navigator.userAgent.match(
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
      ) != null
    ) {
      this.isPhone = true;
    } else {
      this.isPhone = false;
    }
    console.log("Test", document.documentElement.clientWidth);
    if (document.documentElement.clientWidth < 1200) {
      that.isPortrait = true;
    } else {
      that.isPortrait = false;
    }
    window.onresize = () => {
      return (() => {
        window.fullHeight = document.documentElement.clientHeight;
        window.fullWidth = document.documentElement.clientWidth;
        that.windowHeight = window.fullHeight - 100;
        that.windowWidth = window.fullWidth;
        console.log("Window Height: ", that.windowHeight);
        console.log("Window Width: ", that.windowWidth);
        if (that.windowWidth < 1200) {
          that.isPortrait = true;
        } else {
          that.isPortrait = false;
        }
      })();
    };
  },
  data() {
    return {
      isPhone: false,
      isPortrait: false,
      windowHeight: document.documentElement.clientHeight - 100,
      windowWidth: document.documentElement.clientWidth
    };
  },
  watch: {
    isPhone(val) {
      console.log("Phone", val);
    }
  },
  windowWidth(val) {
    if (this.windowWidth < 1200) {
      this.isPortrait = true;
    } else {
      this.isPortrait = false;
    }
    console.log(val);
  }
};
</script>

